@charset "UTF-8";
:root {
  --primary:  #AA0601;
  --secondary: #650300;
  --margin-unit: 8px; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
blog-nav, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, blog-nav, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* rwd img */
img {
  max-width: 100%;
  height: auto; }

/* 全域 border box */
*, *::before, *::after {
  box-sizing: border-box; }

/* utility */
.container {
  max-width: 1100px;
  margin: 0 auto; }
  @media (max-width: 1140px) {
    .container {
      padding: 0px 39px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0px 15px; } }

.d-flex {
  display: flex; }

.flex-column {
  flex-direction: column; }

.justify-content-start {
  justify-content: flex-start; }

.justify-content-center {
  justify-content: center; }

.justify-content-end {
  justify-content: flex-end; }

.justify-content-between {
  justify-content: space-between; }

.align-items-center {
  align-items: center; }

.align-items-end {
  align-items: flex-end; }

.align-content-end {
  align-content: flex-end; }

.flex-wrap {
  flex-wrap: wrap; }

/* 主體 CSS */
a {
  color: #fff;
  display: block;
  text-decoration: none; }

html {
  font-size: var(--font-size); }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei",  'Noto Sans TC', Roboto, "Helvetica Neue", Arial, sans-serif; }

/* nav*/
.nav {
  background-color: var(--primary);
  padding: 30px 88px; }
  .nav ul li {
    margin-left: calc(8 * var(--margin-unit)); }
  .nav ul li a {
    line-height: 30px;
    font-size: 20px;
    padding: 0 12px; }
  .nav ul li a:hover {
    background-color: var(--secondary); }
  @media (max-width: 1140px) {
    .nav {
      padding: 30px 39px; }
      .nav ul li {
        margin-left: calc(5 * var(--margin-unit)); } }
  @media (max-width: 767px) {
    .nav {
      display: block;
      padding: 0px;
      padding-top: 10px; }
      .nav ul {
        margin-top: 10px;
        justify-content: center; }
      .nav ul li {
        width: 50%;
        margin-left: 0px;
        border: 1px solid #fff;
        text-align: center;
        margin: 0 0 -1px -1px; }
      .nav ul li a {
        padding: 8px 0px; } }

.logo a {
  width: 80px;
