  align-items: center;
  margin-top: calc(3 * var(--margin-unit)); }
  .footer-copyright div a {
    margin-right: 38px; }
  .footer-copyright span {
    font-size: 16px; }
  .footer-copyright div {
    display: flex;
    align-items: center; }
  .footer-copyright ul {
